import { useState, useEffect, useCallback } from "react";
import { useParams, Link } from "react-router-dom";
import MyHeader from "./Header";
import {
  getTodayYmd,
  getNextYmd,
  getPrevYmd,
  getTimestampOfDay,
  getTodayDayOfWeekJP,
} from "../lib/Utils";
import db from "../infra/LocalDB";
import AutoSaveTextArea from "./AutoSaveTextArea";
import ReportView from "./ReportView";

const DailyReport = () => {
  const { dateParam } = useParams();
  const currentYmd: string = dateParam ? dateParam : getTodayYmd();
  const nextYmd = getNextYmd(currentYmd);
  const prevYmd = getPrevYmd(currentYmd);

  // 記録日のタイムスタンプ(その日の始まり)
  const targetTimestamp = getTimestampOfDay(currentYmd);
  console.log(`ymd: ${currentYmd}, unix: ${targetTimestamp}`);
  const [note, setNote] = useState("");

  // DBに内容を保存
  const handleSave = useCallback(() => {
    console.log("Save");
    db.reports.put({ date_timestamp: targetTimestamp, note: note });
  }, [targetTimestamp, note]);

  useEffect(() => {
    // Ctrl+sのイベントを上書き
    const handleKeyDown = (event: KeyboardEvent) => {
      if (
        (event.ctrlKey && event.key === "s") ||
        (event.metaKey && event.key === "s")
      ) {
        event.preventDefault(); // デフォルトの保存動作をキャンセルする
        handleSave();
      }
    };

    document.addEventListener("keydown", handleKeyDown);
    return () => {
      document.removeEventListener("keydown", handleKeyDown);
    };
  });

  useEffect(() => {
    // 非同期でDBから取得した初期値をstateに入れるための処理
    const fetchDataAndSetData = async () => {
      const report = await db.reports
        .where({ date_timestamp: targetTimestamp })
        .first();
      if (report) {
        setNote(report.note);
      }
    };
    fetchDataAndSetData();
  }, [targetTimestamp]);

  function resetContents() {
    setNote("");
  }

  return (
    <div>
      <MyHeader />
      <p>
        <Link onClick={resetContents} to={`/daily/${prevYmd}`}>
          Prev
        </Link>{" "}
        |{" "}
        <Link onClick={resetContents} to={`/daily/${nextYmd}`}>
          Next
        </Link>
      </p>
      <h1>
        {currentYmd}({getTodayDayOfWeekJP(currentYmd)})
      </h1>

      <AutoSaveTextArea
        name="report"
        cols={40}
        rows={20}
        placeholder="今日やったこと"
        value={note}
        onChange={setNote}
        onSave={handleSave}
        delay={2000}
      />

      <ReportView value={note} />
    </div>
  );
};

export default DailyReport;
