/**
 * 日報の内容をパースする
 *
 * 出力形式
 * <カテゴリID, 合計時間>のリスト
 *
 * prevTime
 * currentTime
 *
 * 時刻行が出現し、prevTimeと違った場合
 *  タイムボックスを作る
 *  按分する時間を算出(時間間隔 / タスクスタック)
 *  タスクスタックにたまっていたタスクをカテゴリで集計
 *
 * タスク行が出現した場合、タスクスタックをためておく
 *
 * 空行は飛ばす
 * 最後まで到達して終わりの時間
 *
 *
 * パースロジック
 * 時刻部分を抽出
 * index, 利用時間　の箱を作る
 *
 * タスク部分を抽出
 *
 * 例外項目
 * 時間間隔に2行以上が存在した場合、時間間隔を按分する
 */

import { IKeyword } from "../infra/LocalDB";
import { CategoryProcessedTime } from "../model/Report";

// MEMO: インフラ層の分離ができるとベターかもだけど、ページ表示の段階で改装分離できてないので無理にわけなくてもいいかも。ただ、テストするときとかは不便かも

export function parse(note: string, keywords: Array<IKeyword>) {
  const lines = note.split("\n");
  const timePerCategory = new Array<CategoryProcessedTime>();
  let isFirstTime = true;
  let prevMinutes = 0;
  let currentMinutes = 0;
  let tasks: string[] = [];

  const timeRegex = /(\d{1,2}):(\d{2})/;
  const OTHERS_CATEGORY_ID = 9999;

  // TODO: 週次のトータル時間と割合の表示
  // TODO: 集約されたタスクリストをマウスオーバーとかで見れるようにしたい
  // TODO: 画面構成を再検討
  // TODO: パースエラーになるような状態のときは処理を中断したい

  lines.forEach((line) => {
    const timeMatch = timeRegex.exec(line);
    if (timeMatch) {
      // 時刻行だった場合
      const hour = parseInt(timeMatch[1]);
      const minutes = parseInt(timeMatch[2]);
      console.log(`hour: ${hour}, minutes: ${minutes}`);
      if (isFirstTime) {
        // 初回のみの動き
        prevMinutes = hour * 60 + minutes;
        currentMinutes = hour * 60 + minutes;
        isFirstTime = false;
      } else {
        // 通常は一個スライドしていく
        prevMinutes = currentMinutes;
        currentMinutes = hour * 60 + minutes;

        if (tasks.length > 0) {
          // 積んであるタスクスタックからタスクを取り出して時間計上する
          const procTime = (currentMinutes - prevMinutes) / tasks.length;
          // ここでキーワードを使ってカテゴリIDに変換する
          tasks.forEach((task) => {
            const entry: CategoryProcessedTime = {
              categoryId: findCategoryId(task, keywords) ?? OTHERS_CATEGORY_ID,
              processedTime: procTime,
            };
            timePerCategory.push(entry);
          });
          // タスクスタッククリア
          tasks = [];
        }
      }
    } else {
      // 時刻行でなければタスク行なので配列に入れる
      tasks.push(line);
    }
  });

  // カテゴリIDで集約した結果を返す
  return summarize(timePerCategory);
}

function findCategoryId(task: string, keywords: Array<IKeyword>) {
  const hitKeyword: IKeyword | undefined = keywords.find(
    (element: IKeyword) => {
      return task.includes(element.word);
    }
  );
  return hitKeyword?.categoryId;
}

function summarize(
  timePerCategory: Array<CategoryProcessedTime>
): Array<CategoryProcessedTime> {
  const map = new Map<number, number>(); // categoryIdをキーとし、合計processedTimeを値とするMap

  // グルーピングと合計処理
  for (const timeEntry of timePerCategory) {
    const { categoryId, processedTime } = timeEntry;

    if (map.has(categoryId)) {
      // 既にcategoryIdが存在する場合は、processedTimeを加算する
      map.set(categoryId, (map.get(categoryId) || 0) + processedTime);
    } else {
      // categoryIdが存在しない場合は新たにエントリーを作成する
      map.set(categoryId, processedTime);
    }
  }

  const groupedTimePerCategory = new Array<CategoryProcessedTime>();

  // グループ化されたCategoryProcessedTimeのArrayを作成
  for (const [categoryId, processedTime] of Array.from(map.entries())) {
    const groupedEntry: CategoryProcessedTime = {
      categoryId,
      processedTime,
    };
    groupedTimePerCategory.push(groupedEntry);
  }
  return groupedTimePerCategory;
}
