import Dexie from "dexie";

class MyAppDatabase extends Dexie {
  categories!: Dexie.Table<ICategory, number>; // number = type of the primkey
  keywords!: Dexie.Table<IKeyword, number>;
  reports!: Dexie.Table<IReport, number>;

  constructor() {
    super("MyAppDatabase");
    this.version(1).stores({
      categories: "++id, category",
      keywords: "++id, categoryId",
      // timestamp = ms unixtimestamp
      reports: "date_timestamp, note",
    });
  }
}

export interface ICategory {
  id?: number;
  name: string;
}

export interface IKeyword {
  id?: number;
  categoryId: number;
  word: string;
}

export interface IReport {
  date_timestamp: number;
  note: string;
}

const db = new MyAppDatabase();
export default db;

/**
 * 初期化処理.
 */
db.on("populate", populate);

export function resetDatabase() {
  return db.transaction(
    "rw",
    db.categories,
    db.keywords,
    db.reports,
    async () => {
      await Promise.all(db.tables.map((table) => table.clear()));
      await populate();
    }
  );
}

export function addEmptyCategory() {
  db.categories.add({ name: "" });
}

export function deleteCategory(categoryId: number) {
  db.categories.delete(categoryId);
  db.keywords.where({ categoryId: categoryId }).delete();
}

export async function populate() {
  const id = await db.categories.add({
    name: "最初のカテゴリ",
  });
  const id2 = await db.categories.add({
    name: "セカンド",
  });

  await db.keywords.bulkAdd([
    {
      categoryId: id,
      word: "開発",
    },
    {
      categoryId: id,
      word: "テスト",
    },
    {
      categoryId: id2,
      word: "MTG",
    },
    {
      categoryId: id2,
      word: "調査",
    },
  ]);

  /*
  // テスト用の初期値
  await db.reports.add({
    date_timestamp: getTodayTimestamp(),
    note: "aaaa"
  })
  */
}
