import moment, { Moment } from "moment";

export function getTimestampOfDay(ymd: string | null = null): number {
  const targetYmd = ymd ? ymd : getTodayYmd();
  return convertYmdToDate(targetYmd).unix();
}

/**
 * 現在の日付をYYYY-MM-DD形式の文字列で返す
 */
export function getTodayYmd(): string {
  return moment().startOf("day").format().split("T")[0];
}

/**
 * YMD形式の日付文字列から日本語曜日を返す
 */
export function getTodayDayOfWeekJP(ymd: string): string {
  const current = convertYmdToDate(ymd);
  let dayOfWeekJP: string;
  switch (current.day()) {
    case 0:
      dayOfWeekJP = "日";
      break;
    case 1:
      dayOfWeekJP = "月";
      break;
    case 2:
      dayOfWeekJP = "火";
      break;
    case 3:
      dayOfWeekJP = "水";
      break;
    case 4:
      dayOfWeekJP = "木";
      break;
    case 5:
      dayOfWeekJP = "金";
      break;
    case 6:
      dayOfWeekJP = "土";
      break;
    default:
      dayOfWeekJP = "";
      break;
  }
  return dayOfWeekJP;
}

/**
 * baseYMDを基準とした差分日の日付文字列をYYYY-MM-DD形式の文字列で返す
 */
export function getYmd(baseYmd: string, diffDays: number = 0): string {
  const calcDate = convertYmdToDate(baseYmd).add(diffDays, "days");
  // YYYY-MM-DD部分だけ取り出す
  return calcDate.format().split("T")[0];
}

/**
 * 土日をスキップしたときの次の日付YMD
 */
export function getNextYmd(
  baseYmd: string,
  skipWeekend: boolean = true
): string {
  const calcDate = convertYmdToDate(baseYmd);
  // moment: Sunday as 0 and Saturday as 6.
  const currentDayOfWeek = calcDate.day();
  if (currentDayOfWeek === 5) {
    // Friday
    return calcDate.add(3, "days").format().split("T")[0];
  } else {
    return calcDate.add(1, "days").format().split("T")[0];
  }
}

/**
 * 土日をスキップしたときの前の日付YMD
 */
export function getPrevYmd(
  baseYmd: string,
  skipWeekend: boolean = true
): string {
  const calcDate = convertYmdToDate(baseYmd);
  // moment: Sunday as 0 and Saturday as 6.
  const currentDayOfWeek = calcDate.day();
  if (currentDayOfWeek === 1) {
    // Monday
    return calcDate.add(-3, "days").format().split("T")[0];
  } else {
    return calcDate.add(-1, "days").format().split("T")[0];
  }
}

export function convertYmdToDate(ymd: string): Moment {
  const dateRegex = /(\d{4})-(\d{2})-(\d{2})/;
  const match = dateRegex.exec(ymd);
  if (!match) {
    throw new Error(`Invalid date format: ${ymd}`);
  }
  const year = parseInt(match[1]);
  const month = parseInt(match[2]);
  const day = parseInt(match[3]);

  // set時の月は0始まりなので-1する
  return moment()
    .startOf("day")
    .year(year)
    .month(month - 1)
    .date(day);
}
