import React, { useState } from "react";
import db from "../infra/LocalDB";
import { useLiveQuery } from "dexie-react-hooks";
import { ICategory, deleteCategory } from "../infra/LocalDB";

type CategoryProps = {
  category: ICategory;
  onCategoryNameChange: (event: React.ChangeEvent<HTMLInputElement>) => void;
  onKeywordsChange: (event: React.ChangeEvent<HTMLTextAreaElement>) => void;
};

const CategoryView = ({
  category,
  onCategoryNameChange,
  onKeywordsChange,
}: CategoryProps) => {
  const [categoryName, setCategoryName] = useState(category.name);

  const keywords = useLiveQuery(
    () => db.keywords.where({ categoryId: category.id }).toArray(),
    [category.id]
  );
  if (!keywords) return null;

  return (
    <div>
      <label htmlFor="category">Category:</label>
      <input
        type="text"
        value={categoryName}
        name="category"
        onChange={(ev) => {
          // 表示要素を即座に変更するためにstateも更新しておく
          setCategoryName(ev.target.value);
          onCategoryNameChange(ev);
        }}
      />
      <label htmlFor="keywords">Keywords:</label>
      <textarea
        name="keywords"
        cols={40}
        rows={5}
        defaultValue={keywords.map((it) => it.word).join("\n")}
        onChange={onKeywordsChange}
      />
      <button onClick={() => deleteCategory(category.id!)}>DELETE</button>
    </div>
  );
};

export default CategoryView;
