import React, { useState, useEffect, useCallback, useMemo } from "react";
import { useLiveQuery } from "dexie-react-hooks";
import CategoryView from "./CategoryView";
import { resetDatabase, addEmptyCategory } from "../infra/LocalDB";
import MyHeader from "./Header";
import db from "../infra/LocalDB";

const WAIT_TIME_MS = 2000;

// TODO: コンポーネント 案件 タスク の3軸がいいかも。キーワードを廃止して単語の列挙で良いかも
// 階層的に分析したくなるかもだけどどれが上位とかないのだよな
// TODO: カテゴリを月次で設定する
// TODO: 先月のカテゴリ定義をコピーできるようにする
// 案件 コンポーネント タスク種別(開発, テストなど)

const Categories = () => {
  const [saveReqTime, setSaveReqTime] = useState(Date.now());
  const [isSaved, setIsSaved] = useState(true);

  // 保存したい最新のカテゴリとキーワードをIDをキーとして保存するMap構造を用意する
  // reqSaveでそれを更新し、saveAllでそれをDBに反映させる
  // 更新されたカテゴリ名
  const updatedCategory = useMemo(() => {
    // コンポーネントのレンダリング毎に初期化されちゃうと困るので、メモ化する必要があった
    return new Map<number, string>();
  }, []);
  // カテゴリIDをキーとした、更新されたキーワード群
  const updatedKeywords = useMemo(() => {
    return new Map<number, string[]>();
  }, []);

  const writeToDB = useCallback(() => {
    console.log("Save");
    updatedCategory.forEach((v, k) => {
      console.log(`Save category: ${k} -> ${v}`);
      db.categories.put({ id: k, name: v });
    });
    updatedCategory.clear();

    updatedKeywords.forEach((v, k) => {
      console.log(`Save keywords: ${k} -> ${v}`);
      // カテゴリIDに紐づくキーワードを一度消す
      db.keywords.where({ categoryId: k }).delete();
      for (const w of v) {
        db.keywords.add({ categoryId: k, word: w });
      }
    });
    updatedKeywords.clear();
  }, [updatedCategory, updatedKeywords]);

  function reqSave() {
    setIsSaved(false);
    setSaveReqTime(Date.now());
  }

  useEffect(() => {
    const timer = setInterval(() => {
      // console.log(`SaveReq: ${saveReqTime}, isSaved: ${isSaved}`);
      const currentTimestamp = Date.now();
      // 最終操作から一定時間経過したら保存
      if (!isSaved && currentTimestamp - saveReqTime >= WAIT_TIME_MS) {
        setSaveReqTime(currentTimestamp);
        setIsSaved(true);
        writeToDB();
      }
    }, WAIT_TIME_MS);

    return () => clearInterval(timer);
  }, [saveReqTime, isSaved, writeToDB]);

  const categories = useLiveQuery(() => db.categories.toArray());
  if (!categories) return null;

  // 子要素のHTMLエレメントの変更を吸い上げるためのイベントハンドラ
  const handleCategoryNameChange = (index: number, value: string) => {
    console.log(`[Input][Category] id: ${index}, value: ${value}`);
    // 保存リクエスト
    updatedCategory.set(index, value);
    reqSave();
  };
  const handleKeywordsChange = (index: number, value: string) => {
    console.log(`[Input][Keywords] id: ${index}, value: ${value}`);
    // 保存リクエスト
    updatedKeywords.set(index, value.split("\n"));
    reqSave();
  };

  return (
    <div>
      <MyHeader />
      <h1>Category Editor</h1>
      <button onClick={resetDatabase}>RESET</button>
      <p>{isSaved ? "Save" : "Editing..."}</p>

      {categories.map((it) => (
        <CategoryView
          key={it.id}
          category={it}
          onCategoryNameChange={(ev) =>
            handleCategoryNameChange(it.id!, ev.target.value)
          }
          onKeywordsChange={(ev) =>
            handleKeywordsChange(it.id!, ev.target.value)
          }
        />
      ))}

      <button onClick={addEmptyCategory}>ADD</button>
    </div>
  );
};

export default Categories;
