import db from "../infra/LocalDB";
import { useLiveQuery } from "dexie-react-hooks";
import { parse } from "../lib/NoteParser";

type ReportViewProps = {
  value: string;
};

// TODO: 日次の結果表示をもう少し見やすくしたい

const ReportView = ({ value }: ReportViewProps) => {
  const keywords = useLiveQuery(() => db.keywords.toArray(), []);
  const categories = useLiveQuery(() => db.categories.toArray(), []);

  if (!keywords) {
    return <div>NO REPORT</div>;
  }
  const results = parse(value, keywords);
  const totalProcessedTime = results.reduce(
    (acc, cur) => acc + cur.processedTime,
    0
  );

  function getCategoryName(categoryId: number) {
    const name = categories?.find((elem) => elem.id === categoryId)?.name;
    return name ?? "その他";
  }

  return (
    <div>
      {results
        .sort((a, b) => a.categoryId - b.categoryId)
        .map((it) => (
          <div>
            {getCategoryName(it.categoryId)}[
            {(it.processedTime / 60).toFixed(1)}
            h], {((it.processedTime / totalProcessedTime) * 100).toFixed(0)}%
          </div>
        ))}
      <p>Total {(totalProcessedTime / 60).toFixed(1)}h</p>
    </div>
  );
};

export default ReportView;
